body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --font-color: rgb(229, 230, 225);
  --bg-color: #222222;
}

@-webkit-keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@-webkit-keyframes fromTop {
  0% {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes fromTop {
  0% {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@-webkit-keyframes fromBottom {
  0% {
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes fromBottom {
  0% {
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
.text {
    color: rgb(229, 230, 225);
    font-family: 'Lexend Deca', sans-serif;
}

div#body {
    background-image: -webkit-gradient(
        linear,
        left top, left bottom,
        from(#222222ee),
        to(#222222cc)),
        url(https://wallpaperstream.com/wallpapers/full/night-city/Night-City-View-Tokyo.jpg);
    background-image: -webkit-linear-gradient(
        top,
        #222222ee,
        #222222cc),
        url(https://wallpaperstream.com/wallpapers/full/night-city/Night-City-View-Tokyo.jpg);
    background-image: linear-gradient(
        to bottom,
        #222222ee,
        #222222cc),
        url(https://wallpaperstream.com/wallpapers/full/night-city/Night-City-View-Tokyo.jpg);

    height: 100vh;
}

#top-container {
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
    /* transition-delay: 0.25s; */
}
#header-container {
    display: flex;
    flex-direction: row;

    position: absolute;
    top: 0;
    left: 0;

    /* background-color: rgba(0, 0, 0, 0.1); */
    width: 100%;
    height: 50px;
}

.header-item {
    padding: 20px 30px;
    position: relative;
}

#header-container .header-item a {
    color: inherit;
    text-decoration: none;
    -webkit-transition: all 1s;
    transition: all 1s;
}

#header-container .header-item a:hover {
    cursor: pointer;
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
    -webkit-transition: all 1s;
    transition: all 1s;
}

.header-item a ~ .link-highlight {
    margin-top: 3px;
    width: 0%;
    height: 1px;
    margin-left: 50%;

    background-color: var(--font-color);
    -webkit-transition: all 1s;
    transition: all 1s;
}

.header-item a:hover ~ .link-highlight {
    width: 100%;
    margin-left: 0%;
    -webkit-transition: all 0.65s;
    transition: all 0.65s;
}
 

#intro-container {
    padding-top: 30px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    height: 300px;
}


#name {
    font-size: 80px;
    font-family: 'Lexend Deca', sans-serif;
    /* font-weight: bold; */

    -webkit-animation: 1s ease-out 0s 1 fadeIn,
        1s ease-out 0s 1 fromTop;

            animation: 1s ease-out 0s 1 fadeIn,
        1s ease-out 0s 1 fromTop;
}

#slogan {
    font-size: 30px;
    font-family: 'Lexend Deca', sans-serif;
    opacity: 0;

    -webkit-animation: 1s ease-out 0.75s 1 fadeIn,
        1s ease-out 0.75s 1 fromBottom;

            animation: 1s ease-out 0.75s 1 fadeIn,
        1s ease-out 0.75s 1 fromBottom;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes nameIntro {
    0% {
        -webkit-transform: translateY(-30%);
                transform: translateY(-30%);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        opacity: 1;
    }
}

@keyframes nameIntro {
    0% {
        -webkit-transform: translateY(-30%);
                transform: translateY(-30%);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        opacity: 1;
    }
}
